import React, { Component } from 'react';

export default class Page19 extends Component {
    constructor(props) {
        super(props)
        this.state = {
            answer:-1,
            answerTip:-1
        }
    }
    componentDidMount() {

    }
    clickA(){
        this.setState({
            answer:0,
            answerTip:0
        })
    }
    clickB(){
        this.setState({
            answer:1,
            answerTip:1
        })
    }
    render() {
        let {answer,answerTip}=this.state
        return (
            <div className="Game">
                <img className="GameBg" src={require("./images/bg.jpg")} alt="" />
                <img className="GameTitle" src={require("./images/game.png")} alt=""/>
                <div className="OneAB">
                    <div onClick={this.clickA.bind(this)} className="OneA"><img src={require("./images/A.png")} alt=""/></div>
                    <div onClick={this.clickB.bind(this)} className="OneB"><img src={require("./images/B.png")} alt=""/></div>
                    <div><img className={answerTip==0?"cuo":"hide"} src={require("./images/cuo.png")} alt=""/></div>
                    <div><img className={answerTip==1?"dui":"hide"} src={require("./images/dui.png")} alt=""/></div>
                </div>
                <img className={answer<0?"default":"hide"} src={require("./images/default.gif")} alt=""/>
                <img className={answer==0?"fail":"hide"} src={require("./images/fail.gif")} alt=""/>
                <img className={answer==1?"successR":"hide"} src={require("./images/success.gif")} alt=""/>
                <img className="select" src={require("./images/select.png")} alt=""/>
            </div>
        )
    }
}
